.App {
  text-align: center;
  padding: 0;
  margin: 0;
  list-style: none;
  height: 100vh;
  -ms-box-orient: horizontal;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -moz-flex;
  display: -webkit-flex;
  display: flex;

  background-color: lightgray;
  gap: 12px;
}

.row {
  display: block;
  height: 100%;
  -webkit-flex-direction: row;
  flex-direction: row;
}


